/* Contents */

// Home
.home {
  &-masthead {
    position: relative;
    height: 80vh;
    &:before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: linear-gradient(0deg, rgba(2, 32, 45, 0.30) 0%, rgba(2, 32, 45, 0.30) 100%);
      z-index: 3; }
    &-mashteadbg, {
      height: 100%;
      @include imgBg(53% 0 0); }
    &-swiper {
      height: 100%;
      .swiper-slide {
        width: 100%;
        height: 100%;
        @include imgBg(53% 0 0); } }

    &-content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 4;
      display: flex;
      align-items: flex-end;
      padding-bottom: rem(64);
      color: $white;
      span {
        display: block;
        margin-bottom: rem(4); }
      h1 {
        margin-bottom: rem(40); } } }


  &-category {
    .section-title {
      padding-top: rem(48);
      border-top: 1px solid $burgundy;
      .row {
        align-items: flex-end;
        .col-md-6:last-child {
          display: flex;
          justify-content: flex-end; } } }
    &-wrap {
      margin-bottom: rem(-24);
      .col-lg-3 {
        margin-bottom: rem(24); }

      .card-category {
        width: 100%;
        display: block;
        position: relative;
        transition: all .3s ease-in-out;
        overflow: hidden;
        &-img {
          transition: all .3s ease-in-out;
          &:after {
            content: "";
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background: linear-gradient(359deg, rgba(2, 32, 45, 0.50) 0%, rgba(2, 32, 45, 0.00) 100%);
            z-index: 2;
            transition: all .3s ease-in-out; }
          .bg {
            transition: all .3s ease-in-out; }
          @include imgBg(83.331% 0 0); }

        &-name {
          position: absolute;
          left: 0;
          bottom: 0;
          padding: rem(24);
          width: 100%;
          display: flex;
          justify-content: space-between;
          align-items: flex-end;
          z-index: 3;
          h4 {
            @include font-400;
            margin-bottom: 0;
            color: $white; }
          .icon {
            width: rem(24);
            height: rem(24);
 } }            // transition: all .3s ease-in-out

        &:hover {
          .card-category {
            &-img {
              &:after {
                opacity: .75; }
              .bg {
                transform: scale(1.2); } }
            &-name {
              .icon {
                background: url(../images/ic-arrow-circle-white-filled.svg);
                background-size: rem(20);
                background-position: center;
                background-repeat: no-repeat;
                path {
                  fill: transparent; } } } } } } } }

  &-aboutus {
    position: relative;
    &-bg {
      @include imgBg(32.201% 0 0);
      &:after {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: linear-gradient(82deg, rgba(0, 0, 0, 0.50) 0%, rgba(0, 0, 0, 0.00) 51.93%); } }
    &-content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 3;
      padding: rem(64) 0;
      .container {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between; }
      .section-title {
        h2 {
          color: $white; }

        .subdesc {
          color: $white;
          opacity: .75; } }
      .btn-arrow {
        max-width: max-content;
        color: $white;
        border-bottom: 1px solid $white;
        &:after {
          background: url(../images/ic-arrow-right-long.svg);
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center; }
        &:hover {
          border-color: $white; } } } }

  &-solution {
    background: #9CC9CE;
    .card-solution {
      border-top: 1px solid rgba(31, 31, 31, 0.25);
      padding-top: rem(24);
      b {
        color: $dark;
        padding-right: 4px;
        .svg {
          width: rem(24);
          height: rem(24);
          display: inline-flex;
          margin-right: rem(8); } } } }
  &-client {
    .client {
      &-wrap {
        .col-lg-3 {
          border-right: 1px solid rgba(27, 21, 54, 0.1);
          border-top: 1px solid rgba(27, 21, 54, 0.1);
          &:nth-child(4n) {
            border-right: 0; }
          &:nth-child(-n+4) {
            border-top: 0; } } } }
    .client-logo {
      padding: rem(40);
      img {
        width: auto;
        height: rem(64);
        margin: 0 auto;
        object-fit: contain;
        object-position: center; } } }

  &-someproduct {
    background: #F7FCFC;
    .prevnext {
      display: flex;
      gap: rem(16);
      justify-content: flex-end;
      align-items: flex-end;
      height: 100%;
      .prdSwiper-next,
      .prdSwiper-prev {
        width: rem(48);
        height: rem(48);
        border: 1px solid $primary;
        background-color: $white;
        background-image: url(../images/ic-arrow-right-long-blue.svg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: rem(24);
        display: block;
        border-radius: 100px;
        &.swiper-button-disabled {
          opacity: .5; }
        &:hover {
          cursor: pointer; } }
      .prdSwiper-prev {
        transform: rotate(-180deg); } }

    .section-title {
      padding-top: rem(48);
      border-top: 1px solid $primary; }
    .more {
      text-align: center;
      margin-top: rem(48); } } }


// Produk
.produk {
