// Galeri
//   display: block
.galeri {
  margin-top: rem(88); }

.modal-galeri {
  .modal-content {
    background: transparent;
    border: 0;
    position: relative;
    .close {
      position: fixed;
      top: rem(80);
      right: rem(80);
      width: rem(48);
      height: rem(48);
      background: rgba(255, 255, 255, 0.15);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 100px; }

    .box-img {
      width: 100%;
      @include imgBg(68.2% 0 0); }
    .description {
      color: $white;
      padding-left: rem(40);
      h2 {
        margin-bottom: rem(24); } } } }

.grid-masonry {
  &:after {
    content: '';
    display: block;
    clear: both; }
  .grid-sizer,
  .grid-item {
    width: rem(338); }
  .grid-item {
    float: left;
    margin-bottom: rem(24);
    position: relative;
    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover; }
    &.dw {
      width: rem(700); }
    &:after {
      content: "";
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(2, 32, 45, 0.30) 0%, rgba(2, 32, 45, 0.30) 100%);
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      visibility: hidden;
      transition: all .2s ease-in-out; }
    .content {
      width: 100%;
      padding: rem(24);
      color: $white;
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 3;
      transition: all .3s ease-in-out;
      transform: translateY(10px);
      opacity: 0;
      visibility: hidden;
      h3 {
        font-size: rem(24);
        margin-bottom: rem(8); } }
    &:hover {
      cursor: pointer;
      &:after {
        opacity: 1;
        visibility: visible; }
      .content {
        opacity: 1;
        visibility: visible;
        transform: translateX(0); } } } }
// Auth
.auth {
  &-page {
    display: flex;
    flex-wrap: wrap;
    height: 100vh;
    &-form {
      height: 100%;
      flex: 0 0 calc(100% - 59.72%);
      max-width: calc(100% - 59.72%);
      display: flex;
      flex-direction: column;
      padding: rem(40) rem(88);
      padding-left: rem(58);
      padding-top: 0;
      align-items: flex-start;
      justify-content: space-between;
      position: relative;
      .back {
        margin-top: rem(40);
        width: 100%;
        text-align: left;
        padding-left: rem(58);
        .btn-arrow {
          &:hover {
            &:before {
              transform: translateX(-8px) rotate(-180deg); } } } }
      .logo {
        display: inline-flex;
        margin-bottom: rem(24);
        img {
          width: auto;
          height: rem(48); } }
      .h2 {
        color: $dark;
        margin-bottom: rem(24); }
      .auth-form {
        max-width: rem(411);
        padding-left: rem(58); }
      //   .box
      //     max-width: rem(411)
      .footer-desktop {
        color: $slate;
        font-size: 14px;
        text-align: left;
        width: 100%;
        padding-left: rem(58);
        opacity: .5; } }

    &-bg {
      height: 100%;
      flex: 0 0 59.72%;
      max-width: 59.72%;
      position: relative;
      .auth-slider {
        height: 100%;
        width: 100%;
        .swiper-slide {
          display: flex;
          align-items: flex-end;
          height: 100vh;
          padding: 0 0 rem(40) rem(80);
          position: relative;
          background-size: cover;
          background-position: center bottom;
          &:before {
            content: "";
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background: linear-gradient(0deg, rgba(2, 32, 45, 0.30) 0%, rgba(2, 32, 45, 0.30) 100%); }
          .content {
            color: $white;
            max-width: rem(508);
            position: relative;
            z-index: 3; } } } }
    .swiper {
      width: 100%; }
    .swiper-pagination {
      position: absolute;
      padding-right: rem(80);
      right: 0;
      bottom: rem(40); } } }

