                    content: "";
                    width: 16px;
                    height: 16px;
                    background: #F1F3F3;
                    border-radius: 50px;
                    position: absolute;
                    top: 0;
                    right: 3px;
                    z-index: 2; }
                  &:after {
                    content: "";
                    width: 8px;
                    height: 8px;
                    border-radius: 50px;
                    background: $smoke;
                    border-radius: 50px;
                    position: absolute;
                    top: 4px;
                    right: 7px;
                    z-index: 2; } }
                .title {
                  padding-left: rem(8);
                  position: relative;
                  span {
                    display: block;
                    color: $dark; }
                  a {
                    font-size: 14px; }

                  &:after {
                    content: "";
                    width: 1px;
                    height: calc(100% + #{rem(16)});
                    position: absolute;
                    top: rem(-16);
                    left: rem(-12);
                    border-right: 1px dashed $smoke; } }
                &:first-child {
                  .title {
                    &:after {
                      top: 0; } } }
                &:last-child {
                  .title {
                    &:after {
                      top: rem(-32);
                      height: 100%; } } }
                &.current {
                  .date {
                    &:before {
                      background: #FDF2E8; }
                    &:after {
                      background: #B86314; } } }
                &.ondelivery {
                  .date {
                    &:before {
                      background: lighten($primary, 70%); }
                    &:after {
                      background: $primary; } } }
                &.invoicing {
                  .date {
                    &:before {
                      background: lighten(#B86314, 70%); }
                    &:after {
                      background: #B86314; } } }
                &.done {
                  .date {
                    &:before {
                      background: lighten(#17B814, 50%); }
                    &:after {
                      background: #17B814; } } }

                &:first-child:last-child {
                  .date {
                    &:before {
                      top: 4px; }
                    &:after {
                      top: 8px; } }
                  .title {
                    &:after {
                      display: none !important; } } } } } } }

        .product {
          display: flex;
          align-items: center;
          .left {
            flex: 0 0 60%;
            max-width: 60%;
            display: flex;
            align-items: center;
            gap: rem(24);
            h3 {
              font-size: rem(24);
              line-height: 150%;
              margin-bottom: 0;
              a {
                color: $dark;
                &:hover {
                  text-decoration: none;
                  color: $primary; } } }
            .qty {
              color: $muted;
              b {
                color: $dark; } } }

          .right {
            flex: 0 0 40%;
            max-width: 40%;
            display: flex;
            justify-content: flex-end;
            .price-total {
              color: $primary;
              text-align: right;
              small {
                font-size: 14px;
                line-height: 150%; }
              b {
                display: block;
                color: $primary;
                font-size: rem(24); } } } }

        .mid {
          // padding: rem(24)
          padding-left: rem(24);
          padding-right: rem(24);
          background: #FAFAFA;
          border-top: 1px solid #EDF0F2;
          border-bottom: 1px solid #EDF0F2;
          max-height: 0;
          overflow: hidden;
          transition: all .3s ease-in-out;
          h4 {
            color: $dark;
            @include font-600;
            margin-bottom: 6px; }

          table {
            width: 100%;
            th {
              padding: rem(12);
              background: $primary;
              color: $white;
              @include font-400; }
            td {
              padding: rem(12);
              font-size: rem(14);
              line-height: 150%;
              border-bottom: 1px solid #D5DBE1;
              .box-prdct {
                display: flex;
                align-items: center;
                gap: rem(16);
                .photo {
                  width: rem(48);
                  height: rem(48);
                  img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain; } }
                .name {
                  color: $dark; } } } }

          .btn-wrap {
            margin-top: rem(24);
            .btn + .btn {
              margin-left: rem(16); } } }

        .bottom {
          padding: rem(24);
          background: $white;
          display: flex;
          align-items: center;
          justify-content: space-between;
          .last-update {
            color: $slate; }
          .excol {
            &:hover {
              cursor: pointer; } } }
        &.expand {
          border-color: $muted;
          .mid {
            max-height: max-content;
            padding: rem(24); } } } } } }
