/* Fonts */

@include fontGenerator("Inter",(400),(inter-regular));
@include fontGenerator("Inter Medium",(500),(inter-medium));
@include fontGenerator("Inter Semibold",(600),(inter-semibold));
@include fontGenerator("Inter Bold",(700),(inter-bold));





@mixin font-400 {
  font-family: "Inter", sans-serif;
  font-weight: 400; }

@mixin font-500 {
  font-family: "Inter Medium", sans-serif;
  font-weight: 500; }

@mixin font-600 {
  font-family: "Inter Semibold", sans-serif;
  font-weight: 600; }

@mixin font-700 {
  font-family: "Inter Bold", sans-serif;
  font-weight: 800; }





